<template>
<navHeader :totalItem="cardList" />

<div class="wrapper">

  <div class="card__wrap">
    <productCard v-for="(item, idx) in cardList" :key="idx" :cardInfo="item"/>
  </div>

</div>
</template>

<script>
import Data_product from './assets/Data_product.js'
import navHeader from './components/navHeader.vue'
import ProductCard from './components/ProductCard.vue'


export default {
  name: 'App',
  data() {
    return {
      cardList : Data_product,
      cardListOrigin : [...Data_product],
    }
  },
  methods : {
    sortPriceHTL(){
      var wrapper = document.querySelector('.wrapper');
      var cardItem = wrapper.querySelectorAll('.card__item');
      var _this = this;
      this.cardList.sort(function(a, b) {
        var firstIndex = _this.cardList.indexOf(b);
        var secondIndex = _this.cardList.indexOf(a);

        var firstAttr = cardItem[firstIndex].getAttribute('data-current-index');
        var secondAttr = cardItem[secondIndex].getAttribute('data-current-index');

        var firstData = b.modelList[firstAttr].price;
        var secondData = a.modelList[secondAttr].price;

        if (firstData - secondData > 0) return 1;
        if (firstData - secondData == 0) return 0;
        if (firstData - secondData < 0) return -1;
      });
    },
    sortPriceLTH(){
      var wrapper = document.querySelector('.wrapper');
      var cardItem = wrapper.querySelectorAll('.card__item');
      var _this = this;
      this.cardList.sort(function(a, b) {
        var firstIndex = _this.cardList.indexOf(b);
        var secondIndex = _this.cardList.indexOf(a);

        var firstAttr = cardItem[firstIndex].getAttribute('data-current-index');
        var secondAttr = cardItem[secondIndex].getAttribute('data-current-index');

        var firstData = b.modelList[firstAttr].price;
        var secondData = a.modelList[secondAttr].price;

        if (firstData - secondData > 0) return -1;
        if (firstData - secondData == 0) return 0;
        if (firstData - secondData < 0) return 1;
      });
    },
    sortReturn(){
      this.cardList = [...this.cardListOrigin];
    },
  },
  components: {
    navHeader : navHeader,
    productCard : ProductCard,
  }
}
</script>

