<template>
<div class="card__color-item" v-for="(radioItem,j) in colorOption" :key="j" :class="[j == 0 ? 'is-checked' : '', defaultMemorySet !== radioItem.fmyChipList[1].fmyChipCode ? 'is-disabled' : '']" @click="addClass" >
  <button type="button" class="radio-color-btn">
    <span class="card__color-input" :style="{ backgroundColor: radioItem.fmyChipList[0].fmyChipCode }">
      <span class="card__color-label hidden">{{radioItem.fmyChipList[0].fmyChipLocalName}}</span>
    </span>
  </button>
</div>
</template>

<script>
export default {
  name : 'ColorChip',
  props: {
    colorOption: Array,
    optionLocalName: Array,
    defaultMemorySet: Object,
    sortIndex: Number,
  },
  methods: {
    closest: function(el, selector) {
      do {
          if (el.matches ? el.matches(selector) : el.msMatchesSelector(selector)) return el;
          el = el.parentElement || el.parentNode;
      } while (el !== null || el.nodeType === 1);

      return null;
    },
    addClass: function(e) {
      var targetWrap = this.closest(e.target, '.card__color-list');
      var colorChips = targetWrap.querySelectorAll('.card__color-item');
      var target = this.closest(e.target, '.card__color-item');
      [...colorChips].forEach(function(ele) {
        ele.classList.remove('is-checked');
      });
      target.classList.add('is-checked');

      var currentIndex = [...colorChips].indexOf(target);
      this.$parent.currentIndex = currentIndex;
    },
  }
}
</script>

