<template>
    <div class="card__item" :data-current-index="currentIndex" :data-default-memory="defaultMemory" :data-item-index="cardInfo.familyRecord" >

      <div class="card__img">
        <a class="card__link" :href="'http://www.samsung.com'+ cardInfo.modelList[currentIndex].originPdpUrl">
          <img :src="cardInfo.modelList[currentIndex].thumbUrl">
        </a>
      </div>
      <div class="card__title">
        <a class="card__link" :href="'http://www.samsung.com'+ cardInfo.modelList[currentIndex].originPdpUrl">
          <strong class="card__item-name">{{cardInfo.fmyMarketingName}}</strong>
        </a>
      </div>
      <div class="card__option">
        <div class="card__color-name">
          <strong>Colour : </strong>
          <span>{{cardInfo.modelList[currentIndex].fmyChipList[0].fmyChipLocalName}}</span>
        </div>
        <div class="card__color">
          <div class="card__color-wrap" aria-live="polite">
            <div class="card__color-list">
              <colorChip :colorOption="cardInfo.modelList" :colorCode="cardInfo.modelList.fmyChipList" :defaultMemorySet="defaultMemory" :sortIndex="cardInfo.familyRecord" />
            </div>
          </div>
        </div>
        <div class="card__memory">
          <memoryChip :memoryOption="cardInfo.MemoryOptionList" :defaultMemory="cardInfo.MemoryOptionList[0].optionCode" />
        </div>
      </div>
      <div class="card__price">
        <p v-if="cardInfo.modelList[currentIndex].monthlyPriceInfo != null" class="card__price-info">From £{{cardInfo.modelList[currentIndex].monthlyPriceInfo.leasingMonthly}}/mo for {{cardInfo.modelList[currentIndex].monthlyPriceInfo.leasingMonths}}mos at {{cardInfo.modelList[currentIndex].monthlyPriceInfo.interest}} % APR or {{cardInfo.modelList[currentIndex].priceDisplay}}</p>
        <p v-else class="card__price-info">{{cardInfo.modelList[currentIndex].priceDisplay}}</p>
      </div>

      <div class="card__financing-wrap">
        <ul class="card__financing-list">
          <financing v-if="cardInfo.modelList[currentIndex].usp != null" :descList="cardInfo.modelList[currentIndex].usp"/>
          <keysum v-else-if="cardInfo.modelList[currentIndex].usp == null" :descKey="cardInfo.modelList[currentIndex].keySummary"/>
        </ul>
      </div>

      <div class="card__cta-warp">
          <a class="cta cta--contained cta--black" :href="'http://www.samsung.com'+ cardInfo.modelList[currentIndex].originPdpUrl">Buy now</a>
          <a class="cta cta--outlined cta--black" :href="'http://www.samsung.com'+ cardInfo.modelList[currentIndex].originPdpUrl">Learn more</a>
      </div>

    </div>
</template>

<script>
import ColorChip from './ColorChip.vue'
import MemoryChip from './MemoryChip.vue'
import Financing from './Financing.vue'
import Keysum from './Keysum.vue'

export default {
  name : 'ProductCard',
  data() {
    return {
      currentIndex: 0,
      defaultMemory: this.cardInfo.MemoryOptionList[0].optionCode,
      currentMemory: 0,
      itemIndex: 0,
    }
  },
  props: {
    cardInfo: Array,
    cardList : Array,
    colorChip: Array,
    memoryChip: Array,
    currentChildIndex :Array,
  },
  components: {
    colorChip: ColorChip,
    memoryChip: MemoryChip,
    financing: Financing,
    keysum: Keysum,
  }
}
</script>

