<template>
  <button type="button" class="card__memory-btn" v-for="(radioItem,k) in memoryOption" :key="k" :class="k == 0 ? 'is-checked' : ''" @click="addClass">
    <span>{{radioItem.optionCode}}</span>
  </button>
</template>

<script>
export default {
  name : 'MemoryChip',
  props: {
    memoryOption: Array,
  },
  methods: {
    closest: function(el, selector) {
      do {
          if (el.matches ? el.matches(selector) : el.msMatchesSelector(selector)) return el;
          el = el.parentElement || el.parentNode;
      } while (el !== null || el.nodeType === 1);

      return null;
    },
    addClass: function(e) {
      var targetWrap = this.closest(e.target, '.card__memory');
      var memoryChips = targetWrap.querySelectorAll('.card__memory-btn');
      var target = this.closest(e.target, '.card__memory-btn');
      [...memoryChips].forEach(function(ele) {
        ele.classList.remove('is-checked');
      });
      target.classList.add('is-checked');

      this.$parent.defaultMemory = target.innerText;
    }
  }
}
</script>

